
@font-face {
  font-family: "Ringbearer";
  src: url("./assets/RingbearerMedium.ttf") format("truetype");
}

body {
  font-family: "Ringbearer", serif;
}


.jumbotron {

  font-size: 60px;
  color: gold;
  font-family: "Ringbearer", serif;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  text-align: center;
}
@media (max-width: 768px) {
  .jumbotron {
    font-size: 40px;
  }
}
@media (max-width: 480px) {
  .jumbotron {
    font-size: 30px;
  }
}


.toggle-mode {
  transform: translateX(-50%);
  position: fixed;
  bottom: 20px;
  left: 50%;
  z-index: 99;
  background-color: #333;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
}

.toggle-mode:hover {
  background-color: #444;
}