
.sleep-mode-container {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('/public/starry-night.png');
    background-size: cover;
    background-position: center;
    text-align: center;
}
