.interactive-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.interactive {
    position: absolute;
    /*width: 150px; Adjust size */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.2s ease-out;
    pointer-events: none;
}
